import React from 'react'

import * as Yup from 'yup'
import { useSelector } from 'react-redux'
import SettingsForm from './SettingsForm'
import { profileService } from '../../../api/services/profileService'

const SettingsFormContainer = () => {
	const [updateProfile, { isLoading: isUpdatingProfile }] =
		profileService.useUpdateProfileMutation()

	const {
		fullName,
		aboutMe,
		userId,
		lookingForAJob,
		lookingForAJobDescription,
		contacts: { github, vk, facebook, instagram, twitter, website, youtube, mainLink },
	} = useSelector((state) => state.profile.authProfile)

	const initialValues = {
		userId,
		fullName,
		aboutMe,
		lookingForAJob,
		lookingForAJobDescription,
		github,
		vk,
		facebook,
		instagram,
		twitter,
		website,
		youtube,
		mainLink,
	}

	const regExpURL =
		/^((https?):\/\/)?(www.)?[a-z0-9-]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#-]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/i
	const validationSchema = Yup.object({
		fullName: Yup.string().min(2, ' 2 ').required(' '),
		github: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		vk: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		facebook: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		instagram: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		twitter: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		website: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		youtube: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
		mainLink: Yup.lazy((value) =>
			!value ? Yup.string() : Yup.string().matches(regExpURL, ' '),
		),
	})

	const handleOnSubmit = ({
		userId,
		fullName,
		aboutMe,
		lookingForAJob,
		lookingForAJobDescription,
		github,
		vk,
		facebook,
		instagram,
		twitter,
		website,
		youtube,
		mainLink,
	}) => {
		const result = {
			userId,
			fullName,
			aboutMe,
			lookingForAJob,
			lookingForAJobDescription,
			contacts: {
				github,
				vk,
				facebook,
				instagram,
				twitter,
				website,
				youtube,
				mainLink,
			},
		}
		updateProfile(result)
	}

	return (
		<SettingsForm
			validationSchema={validationSchema}
			initialValues={initialValues}
			handleOnSubmit={handleOnSubmit}
			isUpdatingProfile={isUpdatingProfile}
		/>
	)
}

export default SettingsFormContainer
